/**
  *存储sesstionStorage
* */
export const setStore = (name,content) => {
  if(!name) return;
  if(typeof content !== "string"){
    content = JSON.stringify(content);
  };
  return sessionStorage.setItem(name,content);
};

/**
* 获取sesstionStorage
* */
export const getStore = (name) =>{
  if(!name) return;
  return sessionStorage.getItem(name);
}



